<template>
  <div id="wiki" class="ml-4 mr-4 p-4 text-pr w-100 h-75 text-ma ff-sec" v-html="content">
    <!-- <div ></div> -->
  </div>
</template>

<script>
import loadScript from '../helpers/scriptLoader';

export default {
  data(){
    return {
      content: '',
    };
  },
  async mounted(){
    await loadScript('https://cdn.jsdelivr.net/npm/marked/marked.min.js', true);
    const doc = await import('../../public/docs/manual.md');
    // eslint-disable-next-line no-undef
    this.content = marked(doc.default);
  },
  methods: {},
}
</script>

<style>
  /* --t-primary: #4DFFFF;
  --t-secondary: #11a3a3;
  --bg-light: #17666D;
  --bg-primary-l: #083942;
  --bg-primary: #001D28;
  --bg-dark: #001115; */
  #wiki h1{
    font-family: Pixelart;
    color: var(--t-primary);
  }
  #wiki h2{
    font-family: Pixelart;
    color: var(--t-secondary)
  }
  #wiki *:not(pre) code{
    color: var(--t-primary);
    background-color: var(--bg-primary);
    padding:0 .5em;
  }
</style>